.calcOuterWrapper {
    position: absolute;
    display: table;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.calcWrapper {
    display: table-cell;
    justify-content: center;
    vertical-align: middle;
}

.calcContent {
    margin-left: auto;
    margin-right: auto;
    background: #474747;
    height: 75%;
    width: 75%;
    position: relative;
}

.display {
    position: relative;
    background: #000000;
    color: #ffffff;
    height: 15%;
    width: 100%;
    max-width: 100%;
}

.displaySpan {
    position: absolute;
    bottom: 0px;
    right: 3px;
    font-size: 40px;
    max-width: 98%;
    overflow-x: auto;
    flex: 0 0 auto;
}

.equationSpan {
    position: absolute;
    top: 0;
    right: 3px;
    font-size: 15px;
    max-width: 100%;
    overflow-x: auto;
    flex: 0 0 auto;
}

.clear {
    background: #252d4a;
    color: #ffffff;
    width: 75%;
    height: 25%;
    border: thin solid #131624;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    padding: 0px 0px 30px 0px;
}

.numpad {
    height: 65%;
    width: 75%;
    position: absolute;
    bottom: 0;
    left: 0;
}

.num {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #696969;
    color: #ffffff;
    height: 25%;
    border: thin solid #000000;
}

.operationpad {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 25%;
    height: 85%;
    flex-flow: column;
}

.operation {
    background: #4f525c;
    border: thin solid #000000;
    display: flex;
    height: 20%;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: #ffffff;
}
